export default {
  data () {
    return {
      show: true,
      count: null,
      timer: null
    }
  },
  props: {
    timeRange: {
      type: Number,
      default: 0
    },
    endText: {
      type: String,
      default: '已结束'
    },
    callback: {
      type: Function,
      default: ''
    }
  },
  watch: {
    timeRange: function (n, o) {
     // this._test = n
      this.countdown(n)
    }
  },
  mounted () {
    console.log('ragne:' + this.timeRange)
  },
  methods: {
    countdown (timeRange) {
      const TIME_COUNT = timeRange
      if (!this.timer) {
        this.count = TIME_COUNT
        this.show = false
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--
          } else {
            clearInterval(this.timer)
            this.timer = null
            this.content = this.endText
            this._callback()
          }
        }, 1000)
      }
    },
    _callback () {
      if (this.callback && this.callback instanceof Function) {
        this.callback(...this)
      }
    }
  }
}



