
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import CountDown from './plugins/CountDown.vue'
import CountDownse from './plugins/CountDownse.vue'
import AnswerInput from './plugins/AnswerInput.vue'


export default {
components: {
TransferDom,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
CountDown,
CountDownse,
XDialog,
Icon,
Confirm,
AnswerInput
},
directives: {
TransferDom
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
created () {
this.todayQuestion()
},
mounted () {
this.failureShowToast = false
this.changeSuccessShow = false
console.log('fathers: ' + this.clock)

this.getUserInfo()
},
data () {
return {
title: '每日一题',
mobile: '',
password: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
startCount: 0,
endCount: 100,
totalPages: 1002,
failureText: '',
clock: 0,  // answer question time range
blankNum: 0,
isAnswerToday: false,
answerList: {
1: '',
2: ''
},
answerPeopleList: [
{name: 'Tony', getCoins: 20},
{name: 'Mike', getCoins: 20},
{name: 'Xiao', getCoins: 20},
{name: 'Tom', getCoins: 20}
],
mike0: '',
mike1: '',
mike2: '',
questionData: '<p align="center">Today\'s question is not ready!</p>',
questionId: '',
firstAnswer0: '',
firstAnswer1: '',
firstAnswer2: '',
firstAnswer3: '',
secondAnswer: '',
thirdAnswer: '',
submitBtnText: '提交答案',
noActive: true,
disableClass: '',
showTimeOut: false,
answerRightShow: false,
answerWrongShow: false,
deductibleCredits: 0,
increaseCredits: 0,
answerOverShow: false,
overText: '',
viewBoxshow: false

}
},
methods: {
enableSubmit () {
for (let key in this.answerList) {
if (!this.answerList[key].trim()) {
this.noActive = true
break
} else {
this.noActive = false
}
}
},
onInput: function (val) {
this.firstAnswer = val
},
onShow () {
},
onHide () {
},
onHideEvent () {
},
onFailureHide () {

},
onSuccessHide () {
this.$router.push('/home')
},
todayQuestion () {
this.$http.post(this.$apis.todayQuestion, {isToday: true}).then(res => {
if (res.code === 200) {
this.questionId = res.data._id
this.clock = res.data.clock  
this.blankNum = res.data.blankNum

let answerList = {}
for (let i = 0; i < this.blankNum; i++) {
answerList[i + 1] = ''
}
this.answerList = answerList
this.questionData = res.data.sentence
this.deductibleCredits = res.data.deductibleCredits
this.increaseCredits = res.data.increaseCredits
} else {
this.questionData = '<p>Today\'s question is not ready, please wait for a minute!</p>'
}
}).catch(function (error) {
console.log('Error: ' + error)
})
},
submitAnswer () {
let userId = window.localStorage.getItem('userId')

let answerArr = []
for (let key in this.answerList) {
answerArr.push(this.answerList[key].trim())
}

this.$http.post(this.$apis.answer, {
isToday: true,
answer: answerArr,
questionId: this.questionId,
userId: userId
}).then(res => {
if (res.code === 200) {
this.increaseCredits = res.data.increaseCredits
this.answerRightShow = true
} else if (res.code === 202) {  // wrong answer
this.deductibleCredits = res.data.deductibleCredits
this.answerWrongShow = true
} else {  // already answer
this.answerOverShow = true
this.overText = res.msg
}
})

},
timeOutFun () {
console.log('倒计时结束！')
this.submitBtnText = '回答时间到!'
this.disableClass = 'disable-btn'
this.noActive = true
this.showTimeOut = true

},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
onCancel () {
},
onCancelback () {
this.$router.push('home')
},
onConfirm () {
this.$router.push('questionlist')
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
this.isAnswerToday = res.data.isAnswerToday
})
.catch(function (error) {
console.log(error)
})
}
}
}



